<template>
  <div class="collectionPsd" v-if="flag">
    <!-- /收藏列表模板 -->
    <div class="collectionPsd_l">
      <van-image fit="cover" :src="item.image" />
    </div>
    <div class="collectionPsd_r">
      <div class="van-ellipsis">{{ item.store_name }}</div>
      <div>
        <span>￥{{ item.price }}</span
        ><span @click="dela">删除</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["item"],
  data(){
      return{
        //   是否删除
          flag:true,
      }
  },
  created() {
    // console.log(this.item);
  },
  methods:{
      dela(){
          this.flag = false;
          this.$emit("del",this.item.pid);
      }
  }
};
</script>
