<template>
  <div id="collection">
    <van-loading size="24px" vertical v-if="flag">加载中...</van-loading>
    <!-- 我的收藏 -->
    <ul v-if="list.length">
      <li v-for="i in list" :key="i.id">
        <CollectionPsd :item="i" @del="dels"></CollectionPsd>
      </li>
    </ul>
    <!-- 没有收藏时 -->
    <div class="collection_none" v-if="!list.length">
        <CollectionNone></CollectionNone>
    </div>
  </div>
</template>
<script>
import CollectionPsd from "../components/collection/CollectionPsd.vue";
// 没有收藏商品
import CollectionNone from '../components/collection/CollectionNone.vue'
export default {
  components: {
    CollectionPsd,
    CollectionNone,
  },
  data() {
    return {
      list: [],
      flag:true,
    };
  },
  methods: {
    getList() {
      //获取我的收藏列表
      this.axios.get("/api/collect/user?page=1&limit=20").then((res) => {
        // console.log(res);
        this.list = res.data.data;
        this.flag = false;
      });
    },
    dels(val){
      this.flag = true;
        // 取消收藏
        this.axios.post("/api/collect/del",{
          category: "product",
          id:val,
        }).then(()=>{
          this.flag = true;
            // console.log(res);
            this.getList();
        })
    },
  },
  created() {
      this.getList();
  },
};
</script>
<style lang="less" scoped>
#collection{
  background-color: #fff;
}
</style>