</template>
<script>
// 推荐商品
import SearchNone from "../search/SearchNone";
export default {
  components: {
    SearchNone,
  },
  data() {
    return {
      referralArr: null,
    };
  },
  created() {
    // 推荐商品
    this.axios.get("/api/product/hot?page=1&limit=20").then((res) => {
      this.referralArr = res.data.data;
    });
  },
};
</script>
